import React from 'react';
import AllUsers from '../UiComponents/AllUsers';
const MangeProfile = () => {
  return (
    <div className='page manage-profile'>
      <h4>Pending Profiles</h4>
      <AllUsers type='pending' />
      <h4>Approved Profiles</h4>
      <AllUsers type='approved' />
    </div>
  );
};

export default MangeProfile;
