import React, { useEffect, useState } from 'react';
import { message, Table, Tag, Tooltip } from 'antd';
import axios from 'axios';
import { Link } from 'react-router-dom';
import {
  CheckOutlined,
  CloseOutlined,
  DeleteOutlined,
  StopOutlined,
} from '@ant-design/icons';

const AllUsers = ({ type }) => {
  const [data, setdata] = useState();
  const [statusType, setStatusType] = useState(false);
  const [spin, setspin] = useState(false);
  const [refreshKey, setRefreshKey] = useState(1);
  const token = localStorage.getItem('token');

  useEffect(() => {
    if (type === 'approved') {
      setStatusType(true);
    } else if (type === 'pending') {
      setStatusType(false);
    }
    setspin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/profile/users`)
      .then((resp) => {
        setspin(false);
        setdata(resp.data);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, [refreshKey]);

  const handleAction = (value, data) => {
    const id = data.action._id.replace(/['"]+/g, '');
    setspin(true);
    const updatedData = {
      status: value,
    };
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/profile/data/${id}`,
        updatedData,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((resp) => {
        setspin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          setRefreshKey(refreshKey + 1);
        } else {
          message.warning('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setspin(false);
        console.log(err.response);
        message.error('Ouch! An error ocured');
      });
  };

  const approvedColumn = [
    {
      title: '',
      dataIndex: 'dp',
      key: 'dp',
      width: 40,
      render: (url) => (
        <div
          className='table-dp'
          style={{
            backgroundImage: `url('${url}')`,
          }}
        ></div>
      ),
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Role',
      dataIndex: 'role',
      key: 'role',
      render: (userType) => {
        let color = '';
        if (userType == 'Student') {
          color = 'geekblue';
        } else if (userType == 'Mentor') {
          color = 'green';
        } else {
          color = 'volcano';
        }
        return <Tag color={color}>{userType}</Tag>;
      },
    },
    {
      title: 'Address',
      dataIndex: 'address',
      key: 'address',
    },
    {
      title: 'Projects',
      dataIndex: 'projects',
      key: 'projects',
    },
    {
      title: 'Blogs',
      dataIndex: 'blogs',
      key: 'blogs',
    },

    {
      title: 'Action',
      key: 'action',
      width: 100,
      render: (value) => {
        return (
          <div>
            <Tooltip
              placement='topLeft'
              title='Block User'
              onClick={() => handleAction(false, value)}
            >
              <button className='action-btn reject'>
                <StopOutlined className='icon' />
              </button>
            </Tooltip>
          </div>
        );
      },
    },
    {
      title: 'View',
      key: 'view',
      width: 150,
      render: (value) => {
        let link = `/profile/${value.action._id}`;
        return (
          <div>
            <Link to={link} className='profile-btn'>
              View Profile
            </Link>
          </div>
        );
      },
    },
  ];

  const pendingColumn = [
    {
      title: '',
      dataIndex: 'dp',
      key: 'dp',
      width: 40,
      render: (url) => (
        <div
          className='table-dp'
          style={{
            backgroundImage: `url('${url}')`,
          }}
        ></div>
      ),
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Role',
      dataIndex: 'role',
      key: 'role',
      render: (userType) => {
        let color = '';
        if (userType == 'Student') {
          color = 'geekblue';
        } else if (userType == 'Mentor') {
          color = 'green';
        } else {
          color = 'volcano';
        }
        return <Tag color={color}>{userType}</Tag>;
      },
    },
    {
      title: 'Action',
      key: 'action',
      width: 280,
      render: (value) => {
        return (
          <div>
            <Tooltip placement='topLeft' title='Approve'>
              <button
                className='action-btn accept'
                onClick={() => handleAction(true, value)}
              >
                <CheckOutlined className='icon' />
              </button>
            </Tooltip>
            <Tooltip
              placement='topLeft'
              title='Reject'
              onClick={() => handleAction(false, value)}
            >
              <button className='action-btn reject'>
                <CloseOutlined className='icon' />
              </button>
            </Tooltip>
            <Tooltip
              placement='topLeft'
              title='Delete'
              onClick={() => handleAction(false, value)}
            >
              <button className='action-btn delete'>
                <DeleteOutlined className='icon' />
              </button>
            </Tooltip>
          </div>
        );
      },
    },
    {
      title: 'View',
      key: 'view',
      width: 150,
      render: (value) => {
        let link = `/profile/${value.action._id}`;
        return (
          <div>
            {console.log('link at btn', link)}
            <Link to={link} className='profile-btn'>
              View Profile
            </Link>
          </div>
        );
      },
    },
  ];

  let tableData =
    data &&
    data
      .filter((value) => value.status === statusType)
      .map((value, index) => {
        return {
          key: index,
          dp: value.profilePicture,
          name: value.fullname,
          role: value.role,
          projects: 15,
          blogs: 25,
          tags: ['daasdfas'],
          address: 'jk',
          action: value,
          view: value,
        };
      });
  return (
    <div>
      <Table
        columns={statusType ? approvedColumn : pendingColumn}
        dataSource={tableData}
        className='all-user-table'
      />
    </div>
  );
};

export default AllUsers;
